<template>
  <div>
    <v-chart :forceFit="true" :height="height" :data="data" :scale="scale">
      <v-tooltip :showTitle="false" dataKey="item*percent" />
      <v-axis />
      <v-legend dataKey="item" />
      <v-pie position="percent" color="item" :v-style="pieStyle" :label="labelConfig" />
      <v-coord type="theta" />
    </v-chart>
  </div>
</template>

<script>
  const DataSet = require('@antv/data-set');

  const sourceData = [
    { item: '', count: 40 },
    { item: '', count: 21 },
    { item: '', count: 17 },
    { item: '', count: 13 },
    { item: '', count: 9 }
  ];

  const scale = [{
    dataKey: 'percent',
    min: 0,
    formatter: '.0%',
  }];

  const dv = new DataSet.View().source(sourceData);
  dv.transform({
    type: 'percent',
    field: 'count',
    dimension: 'item',
    as: 'percent'
  });
  const data = dv.rows;

  export default {
    name: 'QPieChart',
    props: {
      data: {
        type: Array,
        default: function() {
          return data
        }
      },
      height: {
        type: Number,
        default: 300
      },
      scale: {
        type: Array,
        default: function() {
          return scale
        }
      },
      pieStyle: {
        type: Object,
        default: function() {
          return {
            stroke: "#fff",
            lineWidth: 1
          }
        }
      },
      labelConfig: {
        type: Array,
        default: function() {
          return ['percent', {
            formatter: (val, item) => {
              return item.point.item + ': ' + val;
            }
          }]
        }
      }
    },
  }
</script>

<style scoped>

</style>


