<template>
  <div>
    <v-chart :forceFit="true" :height="height" :data="data">
      <v-tooltip />
      <v-axis />
      <v-bar position="x*y" />
    </v-chart>
  </div>
</template>

<script>
  const data = [
    { x: '', y: [ 76, 100 ] },
    { x: '', y: [ 56, 108 ] },
    { x: '', y: [ 38, 129 ] },
    { x: '', y: [ 58, 155 ] },
    { x: '', y: [ 45, 120 ] },
    { x: '', y: [ 23, 99 ] },
    { x: '', y: [ 18, 56 ] },
    { x: '', y: [ 18, 34 ] },
  ];

  export default {
    name: 'QIntervalChart',
    props: {
      data: {
        type: Array,
        default: function() {
          return data
        }
      },
      height: {
        type: Number,
        default: 300
      }
    },
  }
</script>

<style scoped>

</style>


